@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap');
@font-face {
    font-family: Chronicle;
    src: url('../../fonts/Chronicle\ Disp\ Cond\ Light.otf');
  }
.wrapper {
    background: url('../../images/bg_man.png') no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height:100vh;
  width:100vw;
  margin:0;
  padding:0;
  position:fixed;
}
.blurred_active {
    backdrop-filter:blur(10px);
    opacity:0.9;
    background: rgba(235, 235, 235, 0.9);
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
}
.navigation_active {
    position: absolute;
    left: 75%;
    right: 0%;
    top: 0%;
    transition: display 3s ease-in;
    bottom: 0%;
    background: rgba(23, 23, 23, 0.8);
    mix-blend-mode: multiply;
    font-family: 'Chronicle';
    color: #FFF;
}

.navigation_active a {
    text-decoration: none;
    color: #FFF;
}

.navigation {
    position: absolute;
    left: 75%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    background: rgba(23, 23, 23, 0.8);
    mix-blend-mode: multiply;
    font-family: 'Chronicle';
    color: #FFF;
    display:none;
}
.blurred {
    backdrop-filter:blur(10px);
    opacity:0.9;
    background: rgba(235, 235, 235, 0.9);
    position: absolute;
left: 0%;
right: 0%;
top: 0%;
bottom: 0%;
display:none;
}

.navigation_active h1 {
    color: #fff;
    font-size:32px;
    text-align:center;
}

.navigation_active li {
    text-decoration: none;
    list-style-type:none;
    padding:15px 0px;
    display:flex;
    text-align:left;
    font-size:20px;
    font-family: 'Roboto', sans-serif;
    font-weight:300;
}
.menu_item_active {
    background-color: rgba(0,0,0,0.2);
}
.close_cross {
    margin-top:30%;
}

.navigation_active li img {
    margin-right:40px;
    margin-left:40px;
}

.logo {
    position: absolute;
left: 4.65%;
right: 87.01%;
top: 6%;
bottom: 86.67%;

background: url(../../images/logo.svg);
background-repeat: no-repeat;
mix-blend-mode: multiply;
}
.landing_main {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    height:180px;
    width:540px;
    font-size:36px;
    margin:30vh auto auto 15vw;
}
.close_cross {
    margin-top:30%;
    margin-left:45%;
    
}
.close_cross img {
    height:30px;
    width:30px;
}
.landing_main_text {
}
.landing_main_text p {
    margin:0;
    font-weight:300;
    color: rgba(1, 1, 1, 0.55);
}
.landing_main_buttons {
    font-size:20px;
    margin-top:60px;
}
.landing_main_buttons__learn_more {
    width:137px;
    height:48px;
    border: 1px solid gray;
    padding:6px 14px;
    background-color: #B1956B;
    color: #FFFF;
    cursor: pointer;
}
.landing_main_buttons__join_us {
    width:137px;
    height:48px;
    margin-right:30px;
    padding:6px 14px;
    border: 1px solid black;
    cursor: pointer;
}