.blurred_active {
    backdrop-filter:blur(10px);
    opacity:0.9;
    background: rgba(235, 235, 235, 0.9);
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
}
.navigation_active {
    position: absolute;
    left: 75%;
    right: 0%;
    top: 0%;
    transition: display 3s ease-in;
    bottom: 0%;
    background: rgba(23, 23, 23, 0.8);
    mix-blend-mode: multiply;
    font-family: 'Chronicle';
    color: #FFF;
}

.navigation_active a {
    text-decoration: none;
    color: #FFF;
}

.navigation {
    position: absolute;
    left: 75%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    background: rgba(23, 23, 23, 0.8);
    mix-blend-mode: multiply;
    font-family: 'Chronicle';
    color: #FFF;
    display:none;
}
.blurred {
    backdrop-filter:blur(10px);
    opacity:0.9;
    background: rgba(235, 235, 235, 0.9);
    position: absolute;
left: 0%;
right: 0%;
top: 0%;
bottom: 0%;
display:none;
}

.navigation_active h1 {
    color: #fff;
    font-size:32px;
    text-align:center;
}

.navigation_active li {
    text-decoration: none;
    list-style-type:none;
    padding:15px 0px;
    display:flex;
    text-align:left;
    font-size:20px;
    font-family: 'Roboto', sans-serif;
    font-weight:300;
}
.menu_item_active {
    background-color: rgba(0,0,0,0.2);
}
.close_cross {
    margin-top:30%;
}

.navigation_active li img {
    margin-right:40px;
    margin-left:40px;
}
.close_cross {
    margin-top:30%;
    margin-left:45%;
    
}
.close_cross img {
    height:30px;
    width:30px;
}
.logo {
    position: absolute;
left: 4.65%;
right: 87.01%;
top: 6%;
bottom: 86.67%;

background: url(../../images/logo.svg);
background-repeat: no-repeat;
mix-blend-mode: multiply;
}